import {Dices} from "./Dices";

/**
 * 
 */
export enum BoardActionType {
    "Cube" = 1, "Dice", "Point"
}

/**
 * 
 *
 * 
 */
export type BoardAction =
    { type: BoardActionType.Cube } |
    { type: BoardActionType.Dice } |
    {
        type: BoardActionType.Point
        pos: number
        dices: Dices
    }
